/**=====================
  22. Dashboard CSS Start
==========================**/
// update index dashboard css start
.trasaction-sec {
  &.card {
    .card-body {
      margin-bottom: -35px;
    }
  }
  .transaction-totalbal {
    padding-left: 30px;
    h2 {
      display: flex;
      align-items: center;
      font-size: 22px;
      font-weight: 700;
    }
  }
  #chart-3dash {
    margin-left: -22px;
    margin-right: -12px;
  }
}
.growth-overview,
#chart-dashbord {
  height: 355px;
}
.dashboard-default-sec {
  .rate-sec {
    + .rate-sec {
      .card-primary {
        border-color: rgba($secondary-color, 0.1) !important;
        &:hover {
          .round-box {
            background-color: $secondary-color !important;
            svg {
              fill: $white !important;
            }
          }
        }
        .round-box {
          background-color: rgba($secondary-color, 0.1) !important;
          svg {
            fill: $secondary-color !important;
          }
        }
        i,
        .btn-arrow.arrow-primary {
          color: $secondary-color !important;
        }
        .toprightarrow-primary {
          background-color: rgba($secondary-color, 0.1);
        }
      }
    }
  }
}
// Helper classes page css
.helper-classes {
  padding: 30px;
  margin-bottom: 0;
  white-space: pre-line;
}
// box-shadow
.box-shadow-title {
  .sub-title {
    margin: 30px 0;
  }
}
// list //
.list-group-item {
  background-color: $light-background;
  border-color: $light-gray;
  &.active {
    background-color: var(--theme-default);
    &:hover {
      background-color: var(--theme-default);
      color: $white;
    }
  }
}
.list-group-item-action {
  &:hover,
  &:focus {
    background-color: rgba($primary-color, 0.1);
    color: var(--theme-default);
  }
  &.active {
    background-color: var(--theme-default);
    color: $white;
    &:hover {
      background-color: var(--theme-default);
      color: $white;
    }
  }
}
// general-widget
.general-widget {
  .crm-activity{
    li.media{
      &:nth-child(even){
        span.font-primary{
          color: $secondary-color !important;
        }
      }
    }
  }
  .custom-card {
    .card-social {
      justify-content: center;
      border: unset;
    }
    .card-header {
      img {
        margin-top: -95px;
      }
    }
  }
}
.dashboard-default-sec{
  .card{
    &.income-card {
      .card-body {
        p {
          margin-bottom: 14px !important;
        }
      }
    }
  }
}
.dashboard-default-sec,
.dashboard-2-main {
  .card {
    .card-header {
      i {
        font-size: 14px;
      }
      p {
        font-weight: 500;
        color: $light-text;
        margin: 0;
        font-size: 12px;
      }
      h5 {
        font-size: 18px;
        font-weight: 700;
        margin: 0;
      }
      ul.week-date {
        li {
          display: inline-block;
          font-size: 12px;
          color: $light-font;
          font-weight: 500;
          &:nth-child(n + 2) {
            margin-left: 10px;
            padding-left: 15px;
            border-left: 1px solid $light-gray;
          }
        }
      }
    }
    &.income-card {
      overflow: hidden;
      .card-header {
        padding-bottom: 0;
      }
      .card-body {
        p {
          margin-bottom: 0;
        }
        .round-box {
          width: 55px;
          height: 55px;
          border-radius: 27px;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-left: auto;
          margin-right: auto;
          margin-bottom: 25px;
          transition: all 0.5s ease;
          svg {
            width: auto;
            height: 25px;
            transition: all 0.5s ease;
          }
        }
        .knob-block {
          position: relative;
          input {
            color: $dark-light !important;
            font-size: 16px !important;
          }
          canvas {
            height: 56%;
            width: 56%;
          }
        }
        .round-progress {
          .progress-circle {
            position: relative;
          }
        }
        .parrten {
          position: absolute;
          top: -25px;
          right: -25px;
          svg {
            fill: rgba($primary-color, 0.03);
            width: 100px;
            height: 100px;
            transform: rotate(-135deg);
          }
        }
      }
      &.card-primary {
        .round-box {
          background-color: rgba($primary-color, 0.1);
          svg {
            fill: var(--theme-default);
          }
        }
        &:hover {
          .round-box {
            background-color: var(--theme-default);
            svg {
              fill: $white;
            }
          }
        }
      }
      &.card-secondary {
        border-color: $border-secondary;
        .round-box {
          background-color: rgba($secondary-color, 0.1);
          svg {
            fill: var(--theme-secondary);
          }
        }
        &:hover {
          .round-box {
            background-color: var(--theme-secondary);
            svg {
              fill: $white;
            }
          }
        }
      }
      .card-body {
        h3 {
          font-weight: 700;
        }
        h5 {
          font-size: 22px;
          font-weight: 700;
          text-align: center;
          color: $theme-body-font-color;
        }
        p {
          font-size: 12px;
          color: $light-text;
          font-weight: 500;
          text-transform: capitalize;
          &:last-child {
            font-weight: 700;
          }
        }
      }
    }
  }
}
.center-content {
  margin: auto;
  padding-right: 50px;
  .week-date {
    li {
      font-weight: 500;
      font-size: 12px;
      + li {
        margin-left: 10px;
        padding-left: 15px;
        border-left: 1px solid $light-gray;
      }
    }
  }
}
.right-content {
  margin-left: auto;
  margin-right: 45px;
}
.dashboard-default-sec {
  #chart-dashbord {
    .apexcharts-legend {
      &.position-right,
      &.position-left {
        left: 0 !important;
        right: 0;
        width: fit-content;
        margin-left: auto;
        margin-right: 52%;
      }
    }
  }
}
.position-unset {
  position: unset;
}
.profile-greeting {
  border: none;
  .card-header {
    background-color: transparent;
  }
  h3 {
    color: $white;
    font-weight: 700;
    margin-bottom: 10px;
  }
  p {
    font-size: 14px;
    line-height: 25px;
    color: rgba($white, 0.7);
    font-weight: 400;
    text-align: center;
    font-family: $font-roboto;
    padding: 0 15px;
    margin-bottom: 15px;
  }
  .btn {
    color: var(--theme-default);
    font-weight: 700;
    &:hover {
      background-color: $white !important;
      border-color: $white !important;
    }
  }
  .card-body {
    z-index: 2;
  }
  .confetti {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 1;
  }
  .confetti-piece {
    position: absolute;
    width: 15px;
    height: 15px;
    top: 100%;
    opacity: 0;
    border-radius: 10px 50px 35px 20px;
    animation: makeItRain 5s infinite ease-out;
  }
  .confetti-piece:nth-child(1) {
    left: 7%;
    transform: rotate(-40deg);
    animation: makeItRain 6s infinite ease-out;
  }
  .confetti-piece:nth-child(2) {
    left: 14%;
    transform: rotate(4deg);
    animation: makeItRain 7s infinite ease-out;
  }
  .confetti-piece:nth-child(3) {
    left: 21%;
    transform: rotate(-51deg);
    animation: makeItRain 5s infinite ease-out;
  }
  .confetti-piece:nth-child(4) {
    left: 28%;
    transform: rotate(61deg);
    animation: makeItRain 6s infinite ease-out;
  }
  .confetti-piece:nth-child(5) {
    left: 35%;
    transform: rotate(-52deg);
    animation: makeItRain 5s infinite ease-out;
  }
  .confetti-piece:nth-child(6) {
    left: 42%;
    transform: rotate(38deg);
    animation: makeItRain 4s infinite ease-out;
  }
  .confetti-piece:nth-child(7) {
    left: 49%;
    transform: rotate(11deg);
    animation: makeItRain 8s infinite ease-out;
  }
  .confetti-piece:nth-child(8) {
    left: 56%;
    transform: rotate(49deg);
    animation: makeItRain 7s infinite ease-out;
  }
  .confetti-piece:nth-child(9) {
    left: 63%;
    transform: rotate(-72deg);
    animation: makeItRain 6s infinite ease-out;
  }
  .confetti-piece:nth-child(10) {
    left: 70%;
    transform: rotate(10deg);
    animation: makeItRain 5s infinite ease-out;
  }
  .confetti-piece:nth-child(11) {
    left: 77%;
    transform: rotate(4deg);
    animation: makeItRain 8s infinite ease-out;
  }
  .confetti-piece:nth-child(12) {
    left: 84%;
    transform: rotate(42deg);
    animation: makeItRain 4s infinite ease-out;
  }
  .confetti-piece:nth-child(13) {
    left: 91%;
    transform: rotate(-72deg);
    animation: makeItRain 6s infinite ease-out;
  }
  .confetti-piece:nth-child(odd) {
    background-color: #418578;
  }
  .confetti-piece:nth-child(even) {
    z-index: 1;
  }
  .confetti-piece:nth-child(4n) {
    width: 5px;
    height: 12px;
  }
  .confetti-piece:nth-child(3n) {
    width: 3px;
    height: 10px;
  }
  .confetti-piece:nth-child(4n-7) {
    background-color: #418578;
  }
  @-webkit-keyframes makeItRain {
    from {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    to {
      -webkit-transform: translateY(-350px);
    }
  }
}
