.activity-timeline {
  .media {
    align-items: center;
    .activity-line {
      top: 115px;
      height: 57%;
      position: absolute;
      width: 2px;
      background-color: $light-gray;
      margin: 0 auto;
      left: 36px;
      z-index: 1;
    }
    @each $circle-name, $circle-color in (primary, $primary-color),
      (secondary, $secondary-color), (success, $success-color),
      (danger, $danger-color), (info, $light-font), (light, $light-gray),
      (dark, $dark-color), (warning, $warning-color)
    {
      .circle-dot-#{$circle-name} {
        background-color: rgba($circle-color, 0.25);
        border-radius: 100%;
        padding: 4px;
        font-size: 5px;
        color: $circle-color;
      }
    }
    + .media {
      margin-top: 30px;
    }
    .media-body {
      margin-left: 20px;
    }
  }
}
.twitter-typeahead {
  display: block !important;
}
span.twitter-typeahead {
  .league-name {
    font-size: 16px;
    padding: 6px 10px 0;
  }
  .tt-menu {
    float: left;
    width: 100%;
    min-width: 10rem;
    margin: 0.125rem 0 0;
    font-size: 1rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: $white;
    background-clip: padding-box;
    border-radius: 0.25rem;
    box-shadow: 0 0 20px rgba(89, 102, 122, 0.1);
    border: none;
    padding: 0;
  }
  .tt-suggestion {
    color: $dark-editor-document;
    opacity: 0.6;
    font-size: 13px;
    padding: 6px 12px;
    border-top: 1px solid $light-semi-gray;
    background-color: $light-background;
    cursor: pointer;
    &:hover,
    &:focus {
      background-color: var(--theme-default);
      color: $white;
      opacity: 1;
    }
    &.active,
    &:active {
      color: $white;
      background-color: var(--theme-default);
    }
  }
}
