    }
    .basic-calendar{
      #external-events{
        border: 1px solid $dark-card-border;
      }
      .fc{
        .fc-day-other{
          .fc-daygrid-day-top{
            a{
              color: $dark-small-font-color;
            }
          }
        }
      }  
      table.fc-scrollgrid{
        border: 1px solid $dark-card-border;
      }
    }
    .rbc-calendar{
      .rbc-toolbar{
        button{
          color: $dark-all-font-color;
          &:hover{
            color: $theme-body-font-color;
          }
        }
      }
      .rbc-header{
        border-bottom: 1px solid $dark-card-border;
      }
      .rbc-month-view{
        border: 1px solid $dark-card-border;
      }
      .rbc-month-row{
        + .rbc-month-row{
          border-top: 1px solid $dark-card-border;
        }
      } 
      .rbc-day-bg{
        + .rbc-day-bg{
          border-left: 1px solid $dark-card-border;
        }
      } 
      .rbc-header{
        + .rbc-header{
          border-left: 1px solid $dark-card-border;
        }
      } 
    }
    .rbc-off-range-bg{
      background: $dark-body-background;
    }
    .order-history{
      .rdt_Table{
        .rdt_TableBody{
          .rdt_TableRow{
            &:hover{
              .rdt_TableCell{
                &:first-child{
                  background-color: rgba($dark-card-background, 0.5);
                }
              }
            }
            &:nth-child(odd){
              .rdt_TableCell{
                &:first-child{
                  background-color: rgba($dark-card-background, 0.5);
