      .follow {
        .follow-num {
          color: $dark-all-font-color;
        }
        ul.follow-list {
          border-color: $dark-card-border;
          li {
            &:nth-child(n + 2) {
              border-color: $dark-card-border;
            }
          }
        }
      }
      .post-about {
        ul {
          li {
            h5 {
              color: $dark-all-font-color;
            }
          }
        }
      }
      .profile-post {
        .post-header {
          .media {
            h5 {
              color: $dark-all-font-color;
            }
          }
        }
        .post-body {
          .post-react {
            h6 {
              color: $dark-all-font-color;
            }
            p {
              color: $dark-small-font-color;
            }
          }
