                }
                &:nth-child(4) {
                  border-left: none;
                }
              }
            }
          }
        }
      }
    }
    .bookmark {
      ul {
        li {
          a {
            color: $dark-all-font-color;
          }
        }
      }
    }
    @media screen and (max-width: 480px){
      .page-wrapper{
        .page-main-header{
          .main-header-right{
            .main-header-left{
              padding: 15px 8px;
            }
          }
        }
      }   
    }
  }
  &.dark-sidebar {
    .page-wrapper{
      &.compact-wrapper.modern-sidebar{
        .page-main-header{
          .main-header-left{
            border-radius: 20px 0 0 0;
          }
        }
      }
      &.compact-wrapper.compact-sidebar{
        .page-body-wrapper{
          header.main-nav{
            .main-navbar{
              .nav-menu {
                .dropdown{
                  .menu-title.active{
                    ~ ul{
                      ul.menu-content{
                        background-color: $dark-card-background;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .page-main-header {
      .main-header-right {
        .main-header-left {
          .dark-logo-wrapper {
            display: block;
          }
          .logo-wrapper {
            display: none;
          }
        }
      }
    }
    .page-wrapper {
      &.compact-wrapper {
        .page-body-wrapper {
          header {
            &.main-nav {
              background-color: $dark-card-background;
              border-right: 1px solid $dark-card-border;
              .nav-menu {
                h6 {
                  color: $dark-all-font-color;
                }
                > li {
                  .nav-link {
                    &.active {
                      color: $dark-all-font-color;
                    }
                  }
                  .nav-submenu {
                    li {
                      &:first-child {
                        border-top: 1px solid $dark-card-border;
                      }
                    }
                  }
                  &:last-child {
                    border-bottom: 1px solid $dark-card-border;
                  }
                  > a {
                    color: $dark-all-font-color;
                  }
                }
                .dropdown {
                  ul.nav-submenu {
                    li {
                      &:hover {
                        a {
                          color: $white !important;
                        }
                      }
                      a {
                        color: rgba($white, 0.8) !important;
                        &.active {
                          color: $white !important;
                          text-shadow: 0px 0px $white;
                        }
                      }
                    }
                  }
                }
              }
              .sidebar-user {
                border-color: $dark-card-border;
                ul {
                  li {
                    span {
                      color: $dark-all-font-color;
                    }
                    + li {
                      &:before {
                        background-color: $dark-card-border;
                      }
                    }
                  }
                }
                h6 {
                  color: $dark-all-font-color;
                }
                p {
                  color: $dark-all-font-color;
                }
              }
              .main-navbar {
                .nav-menu {
                  > li {
                    .mega-menu-container {
                      .mega-box {
                        .link-section {
                          .submenu-title {
                            h5 {
                              color: $dark-all-font-color;
                            }
                          }
                          .submenu-content {
                            &.opensubmegamenu {
                              ul {
                                li {
                                  a {
                                    color: $dark-all-font-color;
                                  }
                                }
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
              .sidebar-main-title {
                > div {
                  border-color: $dark-card-border;
                  h6 {
                    color: var(--theme-default);
                  }
                }
              }
            }
          }
          .according-menu {
            i {
              color: $dark-all-font-color;
            }
          }
        }
        .page-main-header {
          border-bottom: 1px solid $dark-card-border;
          .main-header-left {
            background-color: $dark-card-background;
          }
        }
      }
    }
    // sidebar
    .main-navbar {
      ul.nav-pills {
        border-color: $dark-card-border;
        .nav-item {
          &:last-child {
            border-color: $dark-card-border;
          }
          .nav-link {
            color: $dark-all-font-color;
          }
        }
      }
    }
  }
}
@media (max-width: 767px) {
  body {
    &.dark-only {
      .chat-box {
        .people-list {
          ul {
            li {
              border-color: $dark-card-border;
            }
          }
        }
      }
    }
  }
}
