/**=====================
    64. RTL CSS Start
==========================**/
.rtl {
  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        header {
          &.main-nav {
            text-align: right;
            transition: 0.5s;
            border-left: 1px solid $light-gray;
            border-right: unset;
            &.close_icon {
              margin-right: 0;
              margin-left: unset;
              transform: translate(290px);
              ~ .page-body {
                margin-right: 0;
                margin-left: unset;
                transition: 0.5s;
              }
              ~ footer {
                margin-left: 0;
                transition: 0.5s;
              }
              .main-navbar {
                .nav-menu {
                  > li {
                    a {
                      svg {
                        float: left;
                        margin-right: 10px;
                      }
                    }
                  }
                  .dropdown {
                    .menu-title {
                      &.active {
                        ~ .menu-content {
                          display: none !important;
                        }
                      }
                    }
                  }
                }
              }
              .according-menu {
                display: none !important;
              }
            }
            &:hover {
              .main-navbar {
                .nav-menu {
                  .dropdown {
                    .menu-title {
                      &.active {
                        ~ .menu-content {
                          display: block !important;
                        }
                      }
                    }
                  }
                  > li {
                    a {
                      svg {
                        float: none;
                      }
                    }
                  }
                }
              }
              .according-menu {
                display: block;
              }
            }
            .main-navbar {
              .nav-menu {
                & > li {
                  a {
                    .sub-arrow {
                      left: 20px;
                      right: unset;
                    }
                  }
                }
              }
            }
            .sidebar-user {
              ul {
                li {
                  + li {
                    &::before {
                      right: -14px;
                      left: unset;
                    }
                  }
                }
              }
              .setting-primary {
                right: unset;
                left: 35px;
              }
            }
          }
        }
        .page-body {
          margin-left: unset;
          margin-right: 290px;
        }
        .main-nav {
          text-align: right;
          .main-navbar {
            .nav-menu {
              > li {
                a {
                  svg {
                    margin-right: unset;
                    margin-left: 15px;
                  }
                }
              }
            }
          }
        }
        .according-menu {
          right: unset;
          left: 15px;
        }
      }
    }
    &.compact-sidebar {
      &.compact-wrapper {
        .page-body-wrapper {
          header {
            &.main-nav {
              .main-navbar {
                .nav-menu {
                  .dropdown {
                    .menu-title {
                      &.active {
                        ~ .menu-content {
                          left: unset;
                          right: 150px;
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          .page-body {
            margin-right: 150px;
            margin-left: unset;
          }
        }
      }
    }
    &.modern-sidebar {
      &.compact-wrapper {
        footer {
          margin-left: 30px;
        }
        header {
          &.main-nav {
            &.close_icon {
              ~ footer {
                margin-left: 30px;
              }
            }
          }
        }
      }
    }
  }
  .main-navbar {
    ul.nav-pills {
      .nav-item {
        &:last-child {
          border-right: 1px solid $light-gray;
          border-left: unset;
        }
      }
    }
  }
  .blockquote{
    border-right: 4px solid $light-gray;
    border-left: unset;
  }
  .blockquote.text-end{
    border-left: 4px solid $light-gray;
    border-right: unset;
  }
  .blockquote.text-center{
    border: unset;
  }
  ul.list-group {
    i {
      margin-left: unset;
    }
  }
  .rbc-btn-group > button:last-child:not(:first-child) {
    border-radius: 8px 0 0 8px;
  }
  .rbc-btn-group > button:first-child:not(:last-child){
    border-radius: 0 8px 8px 0;
  }
  .post-react{
    .list-group{
      img{
        margin-left: unset;
      }
    }
  }
  .range-slider{
    .row{
      [class*='col-']{
        > div {
          div{
            direction: ltr;
          }
        }
      }
    }
  }
  .vertical-timeline-element-icon{
    svg{
      right: 30%;
      left: unset;
    }
  }
  .deni-react-treeview-item-container{
    .icon-and-text{
      .icon{
        margin-left: 5px;
        margin-right: unset;
      }
    }
  }
  .left-filter{
    .product-filter{
      div.input-range{
        direction: ltr;
      }
    }
  }
  
  .deni-react-treeview-container.classic{
   padding-right: 5px !important;
   padding-left: unset !important;
  } 
  .avatar-showcase {
    .avatar-group {
      .list-group {
        .list-group-item {
          img{
            margin-left: unset;
          }
          + .list-group-item{
